<template>
  <v-row>
  <v-col cols="12">
    <v-avatar class="my-3" size="200">
      <img :src="require('../assets/icon.jpg')">
    </v-avatar>
  </v-col>

  <v-col class="mb-4">
    <h1 class="display-2 font-weight-bold mb-3">
      {{ nameText }}
    </h1>

    <p class="subheading font-weight-regular">
      {{ profileText }}
    </p>
  </v-col>
  </v-row>
</template>

<script>
export default {
  name: 'Profile',

  props: {
    languageId: {
      type: Number,
      default: 0,
    }
  },

  computed: {
    nameText() {
      const ja = '鈴木 健一'
      const en = 'Kenichi Suzuki'
      return [ ja, en ][this.languageId]
    },

    profileText() {
      const ja = '東京大学薬学部4年生 / エンジニア '
      const en = 'The University of Tokyo Faculty of Pharmacy 4th grade / committed in engineering'
      return [ ja, en ][this.languageId]
    }
  }
}
</script>